img {
    width: 30px;
    height: 30px;
}


#container {
    display: flex;
    flex-wrap: wrap;
    /* width: 800px; */
    width: 100%;
    height: 100%;
    /* padding-left: 150px; */
    background-color: grey;
}

#nav {
    height: 200px;
    background-color: grey;
    color: white;
    display: flex;
    padding-top: 12px
}

#nav-left {
    width: 35%;
    background-color: #484848;
    border-radius: 20px;
    padding: 20px;
    margin-left: 10px;
}
#nav-right {
    width: 70%;
    margin-right: 20px;
}

 #nav-left > button {
    /* background-color: rgb(80, 107, 25); */
    color: white;
 }


#canvas{
    background-color: #484848;
    border-radius: 20px;
    height: 100%;
    width: 100%;
    margin-left: 10px;

    border: 1px solid rgb(55, 55, 55);
}


.track {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    border: 1px solid rgb(87, 87, 87);
    display: flex;
    /* width: 750px; */
    width: 100%;
    /* height: 70px; */
    border-radius: 20px;
    padding: 15px;
    background-color: #484848;
}

.track-div-left{
    background-color: rgb(243, 252, 74); 
    /* border-top-left-radius: 20px;
    border-bottom-left-radius: 20px; */
    border-radius: 20px;
    padding-left: 20px;
    padding-top: 7px;
    padding-bottom: 7px;
    margin-right: 15px;
}

button {
    border-radius: 10px;
    /* color: rgb(51, 50, 50); */
    color: white;
    background-color: grey;
    border: 1px solid grey;
    margin: 3px;
    height: 27px;
    width: 65px;
    padding: 5px;
    /* border: none; */
}

button:hover {
    background-color: #484848;
    color: white;
}


select {
    border-radius: 10px;
    border: 1px solid grey;
    /* color: rgb(51, 50, 50); */
    color: white;
    background-color: grey;
    /* border: none; */
    margin: 4px;
    height: 27px;
    padding: 0px;
}

select:hover {
    background-color: #484848;
}


.track-div-right{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    /* background-color: black; */
    border-style: solid;
    border-color: black;
    justify-content: flex-left;
    padding: 0px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;

}

ul {
    display: flex;
    flex-wrap: wrap;
}

li{
    width: 40px;
    height: 60px;
    margin: 7px;
    /* border: 2px solid black; */
    border-radius: 20px;
    border: 1px solid rgb(243, 252, 74);
}

.segment:hover{
    background-color: rgb(243, 252, 74);
    border: 1px solid black;
}
